<template>
  <div class="cash-register">
    <van-nav-bar
      title="会员充值"
      left-text="返回"
      :fixed="true"
      left-arrow
      @click-left="onClickLeft"
    ></van-nav-bar>
    <div class="zhan"></div>
    <van-cell-group>
      <van-field
        v-model="form.memberNo"
        @input="numberInput"
        center
        clearable
        label="会员卡号"
        placeholder="请输入手机号"
        type="number"
      >
        <template #button>
          <van-button size="small" type="info" @click="autMemHandler"
            >验证</van-button
          >
        </template>
      </van-field>
    </van-cell-group>

    <van-cell-group>
      <van-cell title="姓名" :value="name" />
      <van-cell title="剩余金额" :value="money + ' 元'" />
    </van-cell-group>

    <van-cell-group>
      <van-field
        v-model="form.rechargeMoney"
        type="number"
        placeholder="请输入"
        label="充值金额"
      />
      <van-field
        v-model="form.extraMoney"
        type="number"
        placeholder="请输入"
        label="赠送金额"
      />
      <!-- todo -->
      <van-field name="radio" label="付款方式">
        <template #input>
          <van-radio-group v-model="form.payType" direction="horizontal">
            <van-radio
              v-for="item in paytypes"
              :key="item.payType"
              :name="item.payType"
              >{{ item.payTypeName }}</van-radio
            >
          </van-radio-group>
        </template>
      </van-field>
    </van-cell-group>

    <van-cell-group>
      <van-field
        v-model="form.remark"
        rows="2"
        autosize
        label="备注"
        type="textarea"
        maxlength="200"
        placeholder="请输入"
        show-word-limit
      />
    </van-cell-group>

    <div class="btn-wrap">
      <van-button type="info" block @click="submitHandler">提交</van-button>
    </div>
  </div>
</template>

<script>
import { rechargeAdd, queryMemberByNo } from '@/api/index';
import { checkMobile } from '@/utils/index';
import { mapState } from 'vuex';

export default {
  name: 'CashRegister',
  components: {},
  data() {
    return {
      name: '--',
      money: '--',
      form: {
        memberNo: '',
        rechargeMoney: '',
        extraMoney: '',
        remark: '',
        payType: '',
      }
    };
  },
  computed: {
    ...mapState(['paytypes', 'userInfo'])
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    // 会员验证
    autMemHandler() {
      if (checkMobile(this.form.memberNo).code) {
        this.$Toast('请输入会员电话号码');
        return;
      }
      queryMemberByNo(this.form.memberNo).then(res => {
        if (res) {
          if (res.data.code === 0) {
            const { memberName, money } = res.data.data;
            this.name = memberName;
            this.money = money;
          } else {
            this.$Toast(res.data.msg);
          }
        }
      });
    },
    submitHandler() {
      if (this.money === '--') {
        this.$Toast('请先验证会员');
        return;
      }

      if (!this.form.rechargeMoney) {
        this.$Toast('请输入金额');
        return;
      }

      if (!this.form.payType) {
        this.$Toast('请选择付款方式');
        return;
      }

      rechargeAdd({ ...this.form, createBy: this.userInfo.loginName, }).then(res => {
        if (res) {
          if (res.data.code === 0) {
            this.$Toast('充值成功');
            this.$router.go(-1);
          } else {
            this.$Toast(res.data.msg);
          }
        } else {
          this.$Toast('充值失败');
        }
      });
    },
    // 电话号码输入
    numberInput() {
      this.name = '--';
      this.money = '--';
    }
  }
};
</script>


